import { createApp } from "vue";
import { createWebHistory, createRouter } from "vue-router";
import './assets/styles/index.css';


// styles
import "@fortawesome/fontawesome-free/css/all.min.css";
import "@/assets/styles/tailwind.css";


// mounting point for the whole app
import App from "@/App.vue";

// layouts
import Admin from "@/layouts/Admin.vue";
import Auth from "@/layouts/Auth.vue";

// views for Admin layout
import Dashboard from "@/views/admin/Dashboard.vue";
import Settings from "@/views/admin/Settings.vue";
import Tables from "@/views/admin/Tables.vue";
import Maps from "@/views/admin/Maps.vue";

// views for Auth layout
import Login from "@/views/auth/Login.vue";
import Register from "@/views/auth/Register.vue";

// views without layouts
import Landing from "@/views/Landing.vue";
import Sobre_Nosotros from "@/views/Sobre_Nosotros.vue";
import Salud from "@/views/Salud.vue";
import Comercio from "@/views/Comercio.vue";
import Marketing_y_Ventas from "@/views/Marketing_y_Ventas.vue";
import Industria from "@/views/Industria.vue";
import Contacto from "@/views/Contacto.vue";

// routes
const routes = [
  {
    path: "/admin",
    redirect: "/admin/dashboard",
    component: Admin,
    children: [
      {
        path: "/admin/dashboard",
        component: Dashboard,
      },
      {
        path: "/admin/settings",
        component: Settings,
      },
      {
        path: "/admin/tables",
        component: Tables,
      },
      {
        path: "/admin/maps",
        component: Maps,
      },
    ],
  },
  {
    path: "/auth",
    redirect: "/auth/login",
    component: Auth,
    children: [
      {
        path: "/auth/login",
        component: Login,
      },
      {
        path: "/auth/register",
        component: Register,
      },
    ],
  },
  {
    path: "/", // Ruta base apuntando al componente Landing.vue
    component: Landing,
  },
  
  {
    path: "/Sobre_Nosotros",
    component: Sobre_Nosotros,
  },
  {
    path: "/Salud",
    component: Salud,
  },
  {
    path: "/Comercio",
    component: Comercio,
  },
  {
    path: "/Marketing_y_Ventas",
    component: Marketing_y_Ventas,
  },
  {
    path: "/Industria",
    component: Industria,
  },
  {
    path: "/Contacto",
    component: Contacto,
  },
  { path: "/:pathMatch(.*)*", redirect: "/" }, // Redireccin para rutas no encontradas
];

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior(to) {
  if (to.hash === '#rubros') {
    return {
      el: to.hash,
      top: 100, // altura del navbar
      behavior: 'smooth'
    }
  }

  if (to.hash) {
    return {
      el: to.hash,
      behavior: 'smooth'
    }
  }

  return { top: 0 }
}


});

createApp(App).use(router).mount("#app");
