<template>
  <div ref="root" class="lc-root" @mouseenter="pause" @mouseleave="play">
    <div v-for="(logo, i) in visibleLogos" :key="i" class="lc-item" @mouseenter="pause" @mouseleave="play">
      <a :href="logo.url" target="_blank" rel="noopener">
        <img :src="logo.src" alt="logo" @error="onImgError" />
      </a>
    </div>

  </div>
</template>

<script>
export default {
  name: "LogoCarousel",
  props: {
    logos: { type: Array, required: true },
    interval: { type: Number, default: 4000 },
    placeholder: { type: String, default: "/logos/placeholder.png" }
  },
  data() {
    return {
      current: 0,
      intervalId: null,
      isVisible: false
    };
  },
  computed: {
    visibleLogos() {
      const t = this.logos.length;
      const c = this.current;

      return [
        this.logos[(c - 1 + t) % t],
        this.logos[c % t],
        this.logos[(c + 1) % t]
      ];
    }
  },
  methods: {
    step() {
      this.current = (this.current + 1) % this.logos.length;
    },
    play() {
      if (this.intervalId || !this.isVisible) return;
      this.intervalId = setInterval(this.step, this.interval);
    },
    pause() {
      if (!this.intervalId) return;
      clearInterval(this.intervalId);
      this.intervalId = null;
    },
    onImgError(e) {
      e.target.src = this.placeholder;
    },
    observeVisibility() {
      const observer = new IntersectionObserver(
        entries => {
          const entry = entries[0];
          this.isVisible = entry.isIntersecting;

          if (this.isVisible) this.play();
          else this.pause();
        },
        { threshold: 0.2 }
      );

      observer.observe(this.$refs.root);
    }
  },
  mounted() {
    this.observeVisibility();
  },
  beforeUnmount() {
    this.pause();
  }
};
</script>

<style scoped>
/* Contenedor principal */
.lc-root {
  display: flex;
  justify-content: center;
  padding: 18px 0;
  user-select: none;
}

/* Track con 3 elementos fijos */
.lc-track {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 28px;
}

/* Slot de tamao fijo */
.lc-item {
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 900ms cubic-bezier(.2, .9, .2, 1);
  will-change: transform;
  cursor: pointer;
}

/* Imagen */
.lc-item img {
  width: 200px;
  height: 200px;
  object-fit: contain;
  transition: inherit;
}

/* Hover sutil */
.lc-item:hover {
  transform: scale(1.05);
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {

  .lc-item,
  .lc-item img {
    transition: none !important;
  }
}
</style>
