<template>
  <div class="pt-6 lg:pt-12 w-full px-4 text-center flex">
    <div class="relative flex flex-col min-w-0 break-words bg-white w-full mb-8 shadow-2xl rounded-lg h-full card"
      :class="{ 'is-flipped': flipped }" @click="toggleFlip" @keyup.enter.space.prevent="toggleFlip" tabindex="0"
      role="button" aria-pressed="false">
      <div class="card-inner px-4 py-5 flex-auto flex flex-col items-center">

        <!-- FRONT -->
        <div class="card-front absolute inset-0 flex flex-col items-center justify-start px-4 py-5">
          <div :class="['flex items-center justify-center w-12 h-12 mb-5 rounded-full shadow-lg', iconBgClass]">
            <i :class="['fa-solid', frontIcon, 'fa-xl', 'text-white']"></i>
          </div>

          <h6 class="text-2xl font-semibold text-center w-full">{{ frontTitle }}</h6>

          <!-- body flexible: ocupa espacio para empujar el botn al pie -->
          <div class="flex-1 w-full flex flex-col items-center justify-start py-6 gap-3">
            <p v-if="frontText" class="text-lg font-semibold leading-relaxed mt-4 text-blueGray-500 text-center w-full">
              {{ frontText }}
            </p>

            <div v-if="features && features.length" class="flex flex-col items-center w-full gap-3">
              <span v-for="(f, i) in features" :key="i"
                class="px-4 py-2 bg-white rounded-full text-base text-gray-800 w-full text-center">
                {{ f }}
              </span>
            </div>
          </div>

          <!-- FRONT button: evitar que el click burbujee y haga toggle -->
          <router-link v-if="frontLink" :to="frontLink" @click.stop
            :class="['mt-auto inline-block px-6 py-2 text-base font-semibold text-white rounded-lg transition', iconBgClass]">
            {{ frontCta || 'Conoc ms' }}
          </router-link>
        </div>

        <!-- BACK (mismo cono y ttulo que el frente) -->
        <div class="card-back absolute inset-0 flex flex-col items-center justify-start px-4 py-5">
          <!-- header arriba -->
          <div class="w-full flex flex-col items-center">
            <!-- <div :class="['flex items-center justify-center w-12 h-12 mb-4 rounded-full shadow-lg', iconBgClass]">
              <i :class="['fa-solid', frontIcon, 'fa-xl', 'text-white']"></i>
            </div>
            <h6 class="text-2xl font-semibold mb-2 text-center w-full">{{ frontTitle }}</h6> -->
          </div>

          <!-- body flexible: ocupa espacio para que el header quede arriba aun si el texto es corto -->
          <!-- <div class="flex-1 w-full flex items-start">
            <p v-if="backTextComputed" class="text-base text-blueGray-600 mb-4 text-left w-full">
              {{ backTextComputed }}
            </p>
          </div> -->

          <div class="flex-1 w-full flex items-center">
            <div v-if="backTextComputed"
              class="text-base text-blueGray-600 space-y-4 text-justify w-full max-w-[90%] mx-auto">
              <div v-html="backTextComputed"></div>
            </div>
          </div>


          <!-- BACK button: evitar que el click burbujee y haga toggle -->
          <router-link v-if="backLink" :to="backLink" @click.stop
            :class="['mt-auto inline-block px-6 py-2 text-base font-semibold text-white rounded-lg transition', iconBgClass]">
            {{ backCta || 'Conoc ms' }}
          </router-link>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FlipCard",
  props: {
    frontIcon: { type: String, default: "fa-laptop-code" },
    iconBgClass: { type: String, default: "bg-teal-500" },
    frontTitle: { type: String, required: true },
    frontText: { type: String, default: "" },
    features: { type: Array, default: () => [] },
    frontLink: { type: [String, Object], default: null },
    frontCta: { type: String, default: null },

    backTitle: { type: String, default: "" },
    backText: { type: String, default: "" },
    backLink: { type: [String, Object], default: null },
    backCta: { type: String, default: null }
  },
  data() {
    return { flipped: false };
  },
  methods: {
    toggleFlip() {
      this.flipped = !this.flipped;
    }
  },
  computed: {
    // siempre mostrar el mismo ttulo que el frente en el dorso
    backTitleComputed() {
      return this.frontTitle;
    },
    backTextComputed() {
      if (this.backText) return this.backText;
      const parts = [];
      if (this.frontText) parts.push(this.frontText.replace(/\s+$/, '') + '.');
      if (this.features && this.features.length) {
        parts.push('Incluye: ' + this.features.join('; ') + '.');
      }
      return parts.length ? parts.join(' ') : '';
    }
  }
};
</script>

<style scoped>
.card {
  perspective: 1200px;
  cursor: pointer;
  min-height: 400px;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.6s;
  display: flex;
  flex-direction: column;
}

.card.is-flipped .card-inner {
  transform: rotateY(180deg);
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  position: absolute;
  inset: 0;
  box-sizing: border-box;
  background: #ffffff;
  border-radius: 0.75rem;
  box-shadow: 0 25px 50px -12px rgba(2, 6, 23, 0.08);
  padding: 1.25rem;
}

.card-back {
  transform: rotateY(180deg);
}

@media (max-width: 768px) {
  .card:hover .card-inner {
    transform: none;
  }

  .card {
    min-height: 360px;
  }
}
</style>