<script setup>
import { onMounted } from "vue";

onMounted(() => {
  if (!window.TagCanvas) {
    console.error("TagCanvas no est cargado");
    return;
  }

  try {
    window.TagCanvas.Start(
      "myCanvas",
      "tags",
      {
        textColour: "#000",
        outlineMethod: "none",
        reverse: true,
        depth: 0.8,
        wheelZoom: false,
        maxSpeed: 0.01,
        minSpeed: 0.005,
        initial: [0.1, -0.1],
        clickToFront: 0,
      }
    );
  } catch (e) {
    console.error("Error inicializando TagCanvas:", e);
  }
});
</script>

